import React, { useState } from "react";
import RadioButton from "./RadioButton";

import {
    faFaceGrinWide,
    faFaceSmileBeam,
    faFaceGrinStars,
} from "@fortawesome/free-solid-svg-icons";

interface ChildProps {
    // 
    onChangeFlavor: (value: string) => void;
}

/**  */
interface Radio {
    name: string;
    icon: any;
    label: string;
    value: string;
}

const SelectFlavor: React.FC<ChildProps> = ({ onChangeFlavor }) => {
    /** value */
    const [flavor, setFlavor] = useState("");
    const changeAlcohol = (event: React.ChangeEvent<HTMLInputElement>) => {
        const a = event.target.value;
        setFlavor(event.target.value);
        onChangeFlavor(a);
    };
    /**  */
    const flavorList: Radio[] = [
        {
            name: "",
            icon: faFaceGrinWide,
            label: "",
            value: "rich",
        },
        {
            name: "",
            icon: faFaceGrinStars,
            label: "",
            value: "refreshing",
        },
        {
            name: "",
            icon: faFaceGrinWide,
            label: "",
            value: "bitter",
        },
        {
            name: "",
            icon: faFaceSmileBeam,
            label: "",
            value: "sour",
        },
        {
            name: "",
            icon: faFaceGrinStars,
            label: "",
            value: "sweet",
        },
        {
            name: "",
            icon: faFaceGrinStars,
            label: "",
            value: "dry",
        },
        {
            name: "",
            icon: faFaceGrinStars,
            label: "",
            value: "fruity",
        },
        {
            name: "",
            icon: faFaceGrinWide,
            label: "",
            value: "",
        },
    ];
    return (
        <div>
            <RadioButton
                object={flavorList}
                method={changeAlcohol}
                state={flavor}
                radioType={"flavor"}
                title={""}
            />
        </div>
    );
};

export default SelectFlavor;
